@import "../variables.scss";
.media {
    padding: 25px 60px 43px 60px;
    color: $color_orange1;
    font-size: 14px;
    &__nav {
        display: flex;
        column-gap: 80px;
        padding-bottom: 30px;
        & > div {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
        }
        &-slider {
            width: 762px;
        }
        &-text {
            flex-grow: 1;
        }
    }
    &__wrap_content {
        display: flex;
        column-gap: 80px;
    }
    &__slider {
        max-height: 637px;
        height: calc(100vh - 130px);
        max-width: 762px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        .slick-initialized {
            height: 100%;
            background-color: $color_green;
        }
        &-nav {
            margin-top: 36px;
            display: flex;
            justify-content: center;
            column-gap: 60px;
        }
    }
    &__info {
        padding-top: 50px;
        display: flex;
        flex-direction: column;
        &-desc {
            padding-top: 30px;
            word-wrap: break-word;
            word-break: break-all;
        }
        .reviews__list_button {
            position: static;
        }
        .header__nav-buttons {
            margin-top: 80px;
            margin-bottom: 56px;
            flex-direction: column;
            row-gap: 10px;
            font-size: 20px;
        }
        &-more {
            padding-top: 30px;
            text-align: end;
            flex-grow: 1;
        }
    }
    &__item-image {
        padding: 10px 0;
        height: calc(100vh - 150px);
        object-fit: cover; /* Масштабирование картинки с сохранением пропорций и обрезкой лишней части */
        object-position: center; /* Центрирование картинки по горизонтали и вертикали */
    }
}

@media only screen and (max-width: 1050px) {
    .media {
        padding: 12px 12px 50px 12px;
        &__nav {
            position: relative;
            &-slider {
                & > div {
                    display: none;
                }
            }
            &-text {
                h5 {
                    position: absolute;
                    top: 100px;
                    left: 0;
                }
            }
        }
        &__wrap_content {
            margin-top: 98px;
            flex-direction: column;
        }
        &__slider {
            height: 257px;
            &-nav {
                display: none;
            }
        }
        &__info {
            padding-top: 30px;
            &-more {
                margin-bottom: 50px;
            }
        }
        &__item-image {
            height: 257px;
            object-fit: cover; /* Масштабирование картинки с сохранением пропорций и обрезкой лишней части */
            object-position: center; /* Центрирование картинки по горизонтали и вертикали */
        }
    }
}

